<template>
    <div class="card">
        <div class="card-header pb-0">
            <h6>Tambah Admin</h6>
        </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <form role="form" @submit.prevent="submit">
          <table class="table align-items-center mb-0">
            <div class="card-body">
                  <div class="col-md-auto mt-2">
                    <label for="example-text-input" class="form-control-label" required
                      >Nama</label
                    >
                    <input v-model="data.name" class="form-control" type="text" placeholder="masukkan nama admin" />
                  </div>
                  <div class="col-md-auto mt-2">
                    <label for="example-text-input" class="form-control-label" required
                      >Email</label
                    >
                    <input v-model="data.email" class="form-control" type="text" placeholder="masukkan email admin" />
                  </div>
                  <div class="col-md-auto mt-2">
                    <label for="example-text-input" class="form-control-label" required
                      >Password</label
                    >
                    <input v-model="data.password" class="form-control" type="password" placeholder="masukkan password" />
                  </div>
                  <div class="col-md-auto mt-2">
                    <label for="example-text-input" class="form-control-label">Role</label>
                    <select class="form-control" v-model="data.role">
                      <option value="" disabled selected style="text-align: center;">---- Klik Untuk Pilih Role ----</option>
                      <option value="superadmin">Super Admin</option>
                      <option value="admin">Admin</option>
                      <option value="logistik">Logistik</option>
                      <option value="lo">LO</option>
                      <option value="berita">Berita</option>
                    </select>
                  </div>
              </div>
          </table>
          <div class="col-6 text-home px-4 pb-4 pt-0 mt-2">
            <argon-button type="submit" color="dark" variant="gradient" @click="submit">
              <i class="fas fa-plus me-2"></i>
              Tambah Admin
            </argon-button>
          </div>
        </form>
        </div>
      </div>
    </div>
    </template>
    
    <script>
    /* eslint-disable */
    import axios from 'axios';
    import ArgonButton from "@/components/ArgonButton.vue";
  
    export default {
      name: "tambah Admin",
      components: {
      ArgonButton,
    },
    data() {
      return {
        imageUrl: '',
        imageAlt: '',
        data: {
          user:{
            name: '',
            email: '',
            password: '',
            role: '',
            },
        }
      } 
    },
    methods: {
      submit() {
        if (!this.data.name || !this.data.email || !this.data.password || !this.data.role) {
          alert('Harap isi semua field yang dibutuhkan!');
          return;
        }
        const url = `http://127.0.0.1:8000/api/admin/register`;
        const data = {
          name: this.data.name,
          email: this.data.email,
          password: this.data.password,
          role: this.data.role,
        };

        axios.post(url, data)
          .then(response => {
            console.log(response);
            // handle response here
            this.$router.push('/adminpanel'); // Navigate to "/logistik" path
          })
          .catch(error => {
            console.log(error);
            // handle error here
          });
      }
    },
    };
    
  </script>